import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UsuariosService } from '../services/usuarios.service';
import * as firebase from 'firebase';
import { PacienteService } from '../services/paciente.service';

@Component({
  selector: 'app-coordenador',
  templateUrl: './coordenador.page.html',
  styleUrls: ['./coordenador.page.scss'],
})
export class CoordenadorPage implements OnInit {

  titulo:string = 'Pacientes';
  buscando:boolean = true;
  constructor(private router:Router, private usuariosService:UsuariosService, private pacienteService:PacienteService) { }

  async ngOnInit() {
    firebase.auth().onAuthStateChanged(user => {
      this.buscando = user == null
    })
  }

  /**
   * Informa o nome do titulo a ser exibido na página 
   * @param titulo Titulo a ser exibido na página
   */
  setTitulo(titulo:string) {
    this.titulo = titulo;
  }

  /**
   * Desloga o usuário do sitema
   */
  sair() {
    this.usuariosService.deslogar();
    //window.location.reload();
    this.router.navigateByUrl('/login');
  }

}
